import React, { useState } from "react";
import { Form } from "react-bootstrap";
import "../signup.css";
import axios from "axios";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import { HOST } from "../../../constants";


export const SignUp = ({ history }) => {
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    password: "",
    email: "",
    contact: "",
    address: "",
    pincode: "",
  });
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
    // Resetting errors
    if (formData[name]) {
      errors[name] = "";
      setErrors(errors);
    }
  };

  const validateForm = () => {
    let isValid = true;
    const newErrors = {};

    // Validate name
    if (!formData.firstName.trim()) {
      newErrors.firstName = "Name is required";
      isValid = false;
    }

    // Validate username
    if (!formData.lastName.trim()) {
      newErrors.lastName = "last name is required";
      isValid = false;
    }

    // Validate address
    if (!formData.address.trim()) {
      newErrors.address = "address is required";
      isValid = false;
    }

    // Validate pincode
    if (!formData.pincode) {
      newErrors.pincode = "pincode is required";
      isValid = false;
    }

    // Validate password
    if (!formData.password.trim()) {
      newErrors.password = "Password is required";
      isValid = false;
    }

    // Validate email
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!formData.email.trim() || !emailRegex.test(formData.email)) {
      newErrors.email = "Enter a valid email address";
      isValid = false;
    }

    // Validate phone number
    const phoneRegex = /^\d{10}$/;
    if (!formData.contact.trim() || !phoneRegex.test(formData.contact)) {
      newErrors.contact = "Enter a valid 10-digit phone number";
      isValid = false;
    }

    setErrors(newErrors);
    return isValid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    // Add validations here - Create a function
    if (!validateForm()) {
      // If form is valid, you can proceed with signup logic here
      // Show toast here
      console.log("Invalid form");
    }
    // Api call to send create user
    try {
      let response = await axios.post(
        `${HOST}/api/user/signup/`,
        {
          ...formData,
        }
      );
      console.log("response signup", response);
      if (response.status === 201) {
        // history.push("/user/home", { userData: response?.data || {} });
        history.push("/user/login");
        console.log("response", response);
      }
    } catch (error) {
      console.log("error", error);
    }
  };
  return (
    <div className="sign-in__wrapper" >
      {/* <div className="sign-in__backdrop"></div> */}
      <Form className="shadow p-4 bg-white rounded" style={{marginTop : "10%"}}>
        <h3>Sign Up</h3>
        <div className="mb-3">
          <label>First name</label>
          <input
            type="text"
            name="firstName"
            onChange={handleChange}
            className="form-control"
            placeholder="First name"
          />
          {errors.firstName && (
            <span className="red-text">{errors.firstName}</span>
          )}
        </div>
        <div className="mb-3">
          <label>Last name</label>
          <input
            type="text"
            onChange={handleChange}
            name="lastName"
            className="form-control"
            placeholder="Last name"
          />
          {errors.lastName && (
            <span className="red-text">{errors.lastName}</span>
          )}
        </div>
        <div className="mb-3">
          <label>Email address</label>
          <input
            onChange={handleChange}
            type="email"
            name="email"
            className="form-control"
            placeholder="Enter email"
          />
          {errors.email && <span className="red-text">{errors.email}</span>}
        </div>
        <div className="mb-3">
          <label>Contact</label>
          <input
            onChange={handleChange}
            type="number"
            name="contact"
            className="form-control"
            placeholder="Enter contact"
          />
          {errors.contact && <span className="red-text">{errors.contact}</span>}
        </div>
        <div className="mb-3">
          <label>Password</label>
          <input
            onChange={handleChange}
            type="password"
            name="password"
            className="form-control"
            placeholder="Enter password"
          />
          {errors.password && (
            <span className="red-text">{errors.password}</span>
          )}
        </div>
        <div className="mb-3">
          <label>address</label>
          <textarea
            name="address"
            className="form-control"
            placeholder="Enter address"
            onChange={handleChange}
          />
          {errors.address && <span className="red-text">{errors.address}</span>}
        </div>
        <div className="mb-3">
          <label>pincode</label>
          <input
            onChange={handleChange}
            type="number"
            name="pincode"
            className="form-control"
            placeholder="Enter pincode"
          />
          {errors.pincode && <span className="red-text">{errors.pincode}</span>}
        </div>
        <div className="d-grid">
          <button
            type="submit"
            className="btn btn-primary"
            onClick={handleSubmit}
          >
            Sign Up
          </button>
        </div>
        <p className="forgot-password text-right">
          Already registered <Link to="/user/login">Log In</Link>
        </p>
      </Form>
    </div>
  );
};
