import React from "react";
import { Button, Form } from "react-bootstrap";

export const ReceiverDetails = (props) => {
  const {
    setReceiverName,
    setReceiverContact,
    setReceiverAddress,
    setReceiverPincode,
    receiverErrors,
    receiverAddress,
    submitUser,
  } = props;
  console.log("props", props);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        width: "98vw",
        alignItems: "center",
        flexDirection: "column",
        height: "50vh",
        marginTop: 50,
        submitUser,
      }}
    >
      <Form
        className="shadow p-4 bg-white rounded"
        style={{
          width: "35rem",
          maxWidth: "90%",
          zIndex: 1,
        }}
      >
        <div className="h4 mb-2 text-center">
          <div>Enter receiver details</div>
        </div>
        <div className="mb-2">
          <label>Name</label>
        </div>
        <div className="mb-2">
          <input
            type="text"
            value={props.receiverName}
            placeholder="Name"
            onChange={(e) => setReceiverName(e.target.value)}
            className="form-control"
          />
          {receiverErrors.receiverName && (
            <span className="red-text">{receiverErrors.receiverName}</span>
          )}
        </div>
        <div className="mb-2">
          <label>Contact</label>
        </div>
        <div className="mb-2">
          <input
            type="number"
            value={props.receiverContact}
            placeholder="contact"
            onChange={(e) => setReceiverContact(e.target.value)}
            className="form-control"
            required
          />
          {receiverErrors.receiverContact && (
            <span className="red-text">{receiverErrors.receiverContact}</span>
          )}
        </div>
        <div className="mb-2">
          <label>Address</label>
        </div>
        <div className="mb-2">
          <textarea
            name="address"
            className="form-control"
            placeholder="Enter address"
            onChange={(e) => setReceiverAddress(e.target.value)}
            value = {receiverAddress}
          />
          {receiverErrors.receiverAddress && (
            <span className="red-text">{receiverErrors.receiverAddress}</span>
          )}
        </div>
        <div className="mb-2">
          <label>Pincode </label>
        </div>
        <div className="mb-2">
          <input
            type="number"
            value={props.receiverPincode}
            placeholder="pincode"
            onChange={(e) => setReceiverPincode(e.target.value)}
            className="form-control"
            required
          />
          {receiverErrors.receiverPincode && (
            <span className="red-text">{receiverErrors.receiverPincode}</span>
          )}
        </div>
        <div className="mb-2">
          <Button
            style={{ marginTop: 10 }}
            variant="primary"
            onClick={submitUser}
          >
            Continue
          </Button>
        </div>
      </Form>
    </div>
  );
};
